// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-very-dark:                   #041827;
$color-default-white:               #f8fdff;
$color-font-white:                  #f2f5f6;
$color-blue:                        #00b2ff;
$color-grey:                        #d8d8d9;
$color-light-grey:                  #ebebeb;
$color-dark-grey:                   #7f7f85;
$color-card-background:             #c5c4c9;
$color-input-background:            rgba(4, 20, 39, 0.15);
$color-input-focus:                 #9ecbf5;
$color-menu-opened:                 #8095b3;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-dark-blue:                   #011c40;
$color-torch-red:                   #ff1553;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13

$ff-placeholder-main-regular:                  "RobotoCondensed-Regular", "Arial", sans-serif;
$ff-placeholder-main-bold:                     "RobotoCondensed-Bold", "Arial", sans-serif;
$ff-placeholder-decorative-regular:            "PTMono-Regular", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-770:                            770px;
$vp-830:                            830px;
$vp-1024:                           1024px;
$vp-1095:                           1095px;
$vp-1170:                           1170px;
$vp-1280:                           1280px;
$vp-1366:                           1366px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
